import { BrowserRouter, Switch, Route } from "react-router-dom";
import { useEffect } from "react";
import { connect } from "react-redux";
import {getUser} from '../actions/'
// import Loader from './Loader';
// import Error404 from "./views/Error404";
import AdminRoutes from "./routes/AdminRoutes";
// import FullScreenModal from './FullScreenModal';
import Layout from "./Layout";
import Home from "./views/Home";
import About from "./views/About";
import Login from "./views/Login";
import Signup from "./views/Signup";
import Contact from "./views/Contact";
import ProductPage from "./views/ProductPage";
// import Favourites from "./views/Favourites";
import Category from "./views/Category";
import SearchResult from "./views/SearchResults";
import CartView from "./views/CartView";
import Order from "./views/Order";
import VerifyView from "./views/VerifyView";
import Loader from "./Loader";

function App({getUser}) {
  useEffect(()=>{
    getUser()
  }, [getUser])
  return (
    <div className="App">
      <BrowserRouter>
        <Switch>
          <Route exact path="/" render={(props) =>(<Layout><Home {...props} /></Layout>)}/>

          <Route path="/signup" render={(props) =>(<Layout><Signup {...props} /></Layout>)}/>

          <Route path="/login" render={(props) =>(<Layout><Login {...props} /></Layout>)}/>

          <Route path="/about" render={(props) =>(<Layout><About {...props} /></Layout>)}/>

          <Route path="/contact" render={(props) =>(<Layout><Contact {...props} /></Layout>)}/>

          <Route path="/product/:product" render={(props) =>(<Layout><ProductPage {...props} /></Layout>)}/>

          <Route path="/category/:categoryName" render={(props) =>(<Layout><Category {...props} /></Layout>)}/>

          <Route path="/search/:query" render={(props) =>(<Layout><SearchResult {...props} /></Layout>)}/>

          {/* <Route path="/favourites" render={(props) =>(<Layout><Favourites {...props} /></Layout>)}/> */}

          <Route path="/cart" render={(props) =>(<Layout><CartView {...props} /></Layout>)}/>

          <Route path="/create-order" render={(props) =>(<Layout><Order {...props} /></Layout>)}/>

          <Route path="/auth/verify" render={(props) =>(<Layout><VerifyView {...props} /></Layout>)}/>

          {/* <Route path='/test' render={(props) =>(<FullScreenModal {...props}/>)}></Route> */}

          <AdminRoutes/>

        </Switch>
      </BrowserRouter>
      <Loader/>
    </div>
  );
}

export default connect(null, {getUser})(App);
